import style from './requestData.module.css';
import imgRequest from "../../images/requestData.png";
import { useContext } from 'react';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';
import { Link } from 'react-router-dom';

function RequestData() {
  const {store} = useContext(Context);

	return (
	  <section className={style.requestData}>
        <div className={style.text}>
            <h1 className={style.header}>        </h1>
            <p className={style.pharagraph}>  ,     PDF   .</p>
            {store.isAuth && <Link to="/search"><button className={style.button}> </button></Link>}
        </div>
        <img className={style.image} src={imgRequest} alt=" " />
      </section>
	);
  }

  export default observer(RequestData);